

















import Vue from 'vue'
import firebase from 'firebase/compat/app';

export default Vue.extend({
  data() {
    return {
      valid: false,
      email: '',
      password: '',
      error: '',
      loading: false
    }
  },

  methods: {
    submit () {
      const refForm = this.$refs.form as any
      this.valid = refForm.validate()
      if (this.valid) {
        this.loading = true
        firebase
          .auth()
          .signInWithEmailAndPassword(
            this.email,
            this.password
          ).then(() => {
            // this.$router.replace('/')
            this.loading = false
          }).catch(err => {
            this.error = err.message
            this.loading = false
          });
      }
    }
  }
})
