















import Vue from 'vue'
import { useStore } from '@/stores/main'
import firebase from 'firebase/compat/app';
import { mapState } from 'pinia';

export default Vue.extend({
  name: "HeaderBar",

  methods: {
    logOut() {
      firebase
        .auth()
        .signOut()
    }
  },

  computed: {
    ...mapState(useStore, ['user'])
  }
});
